
.skills-table
{
  width: 100%;
  border: none;
}

.skills-table tr
{
  background-color: #14232b;
  margin-bottom: 50px;
}

.skill-item
{
  background-color: white;
  color: black;
  margin: 10px;
  display: inline-flex;
  padding: 2px;

  border-radius: 5px;
}

.skills-title
{
  text-align: right;
  padding-right: 10px;
  padding-left: 40px;
}

.skills-table .space
{
  height: 10px;
}





.skills-table-new
{
  /* background-color: #14232b; */
  background-color: #101b25;
  /* background-color: #080f16; */
  margin-bottom: 10px;
  border-radius: 5px;
}

.skills-title-new
{
  margin-left: 10px;
  margin-right: 10px;
}

.skill-item-new
{  
  /*background-color: #245061;*/  
  background-color: #163341;
  color: white;
  margin: 10px;
  display: inline-flex;
  padding: 4px;

  border-radius: 5px;
}


#intro-text
{
  display: inline-block;
  max-width: 360px;
}

#portrait-img
{
  width: 200px;
  
  display: inline-block;
}

@media screen and (max-width: 600px)
{
  #intro-text
  {
    max-width: 100%;
  }

  #portrait-img
  {
    margin: auto;
    display: block;
  }
}