


.inner-navbar
{
  background-color: #5c707a;
  display: block;
  border-radius: 5px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  padding-left: 20px;
}

.inner-navbar *
{
  display: inline-block;
}

.inner-navbar-element
{
  /*background-color: #163341;*/
  margin-top: 10px;
  
  margin-right: 10px;
  padding: 10px;
  padding-bottom: 10px;


  padding-left: 70px;
  padding-right: 70px;

  border-radius: 5px;

  margin-left: 100px;

}

.inner-navbar a
{
  text-decoration: none;
  color: white;
}


.dynamic-style
{
  background-color: #163341;
}



.inner-navbar-title
{
  background-color: #5c707a;
  margin-bottom: 0;
  padding: 20px;
  margin-top: 50px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  /*color: #163341;*/
  color: #12273d;
}


.heading-wrapper
{
  background-color: #5c707a;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;

  margin-top: 50px;


  position: relative;
}

.heading-wrapper h1
{
  padding-top: 5px;
  margin-top: 5px;
  padding-bottom: 5px;
  margin-bottom: 5px;
  margin-right: 100px;
}

.heading-wrapper  *
{
  display: inline-block;
}


.language-switch
{
  /*
  background-color: black;
  */

  width: 60px;
  height: 40px;

  border-radius: 20px;
  margin: 10px;


  border: 1px solid white;
  background-size: 60px 40px;
}

.temp-button
{
  color: white;
  padding: 5px;
  background-color: #8f9fa0;
  user-select: none;
  border: 1px solid white;
  border-radius: 5px;
  margin: 5px;
  margin-left: 120px;


  position: absolute;
  right: 1px;
}

.temp-button:hover
{
  background-color: #163341;
}

.temp-button:active
{
  background-color: white;
  color: black;
}


@media screen and (max-width: 500px)
{
  .temp-button
  {
    font-size: 14px;
  }
}

@media screen and (max-width: 430px)
{
  .temp-button
  {
    font-size: 12px;
  }
}